/**
 * @description 防抖
 * @author zhxy
 * @date 2021-07-21
 * @export
 * @param {function} fn
 * @param {*} delay
 * @returns {function}
 */
export function debounce(fn, delay) {
  let timer = null;
  return function() {
    const context = this;
    const args = arguments;
    if (timer) {
      clearTimeout(timer);
    }
    timer = setTimeout(function() {
      fn.apply(context, args);
    }, delay);
  };
}
