// import { DEVICE_STATE } from '@/utils/constantsFile';
import { debounce } from '@/utils/debounce';
// 甘特图图例颜色
// const color = ['', '#0871dc', '#f39800', '#666666', '#ff0000'];
// loading图标选项
const loadingOption = {
  text: '',
  spinnerRadius: 20,
  color: '#0871dc'
};
export default {
  name: 'echarts-gantt',
  props: ['chartData', 'loading'],
  data() {
    return {
      myChart: null,
      // 没有数据的标志，显示无数据
      noData: false
    };
  },
  watch: {
    chartData: function() {
      if (this.chartData) {
        this.draw();
        this.noData = false;
      } else {
        this.noData = true;
      }
    },

    loading: function(current) {
      if (!this.myChart) { this.myChart = this.$echarts.init(this.$refs.echartGantt); }
      if (current) {
        this.myChart.showLoading('default', loadingOption);
      } else {
        this.myChart.hideLoading();
      }
    }
  },
  mounted() {
    this.draw();
  },
  methods: {
    draw() {
      if (!this.chartData) return;
      this.myChart = this.$echarts.init(this.$refs.echartGantt);
      // const legendData = [];
      const stateEnum = {
        deactivateValue: '停用',
        exceptionValue: '异常',
        maintainValue: '保养',
        materialWaitingValue: '待料',
        preserveValue: '维护',
        productValue: '生产'
      };
      const colorEunm = {
        deactivateValue: '#FFD307',
        exceptionValue: '#FC4A45',
        maintainValue: '#07aaff',
        materialWaitingValue: '#00E62C',
        preserveValue: '#FF6B22',
        productValue: '#007E2E'
      };
      const series = [];
      for (const key in stateEnum) {
        const temp = {
          name: stateEnum[key],
          type: 'bar',
          label: {
            show: true,
            position: 'top',
            color: 'auto'
          },
          itemStyle: {
            normal: {
              color: colorEunm[key]
            }
          },
          data: this.chartData.map(item => item?.ganttValue[key].toFixed(2))
        };
        series.push(temp);
      }
      this.myChart.setOption({
        tooltip: {
          formatter: function(params) {
            return `${params.seriesName}:${params.value.toFixed(2)}`;
          }
        },
        legend: {
          show: true,
          top: '1%',
          left: 10,
          textStyle: {
            fontSize: 14
          }
          // data: legendData
        },
        toolbox: {
          show: true,
          right: 16,
          itemSize: 25,
          feature: {
            saveAsImage: {
              show: true
            }
          }
        },
        dataZoom: [
          {
            type: 'slider',
            filterMode: 'weakFilter',
            top: 'bottom'
          },
          {
            type: 'inside',
            filterMode: 'weakFilter'
          }
        ],
        grid: {
          containLabel: true,

          left: '3%',
          right: '6%'
        },
        xAxis: {
          type: 'category',
          name: '设备',
          data: this.chartData.map(item => item?.deviceName)
        },
        yAxis: [
          {
            type: 'value',
            name: '小时'
          }
        ],
        series
      });
      const handleResize = debounce(this.myChart.resize, 300);
      window.addEventListener('resize', () => {
        handleResize();
      });
    },
    /**
     * @description: 格式化甘特图数据项
     * @param {*} data
     * @return {*}
     */
    formatMaintData(data) {
      const res = [];
      data.forEach((d, index) => {
        d.deviceLiveReportVo.timeList.forEach(t => {
          res.push({
            name: t.state - 1,
            value: [index, t.startTime, t.endTime]

          });
        });
      });

      return res;
    },
    // 生成Y轴标签
    formatLabel(data) {
      return data.map(el => el.deviceName);
    },
    // 自定义系列绘制函数
    renderRect(params, api) {
      const index = api.value(0);
      const start = api.coord([api.value(1), index]);
      const end = api.coord([api.value(2), index]);
      const height = Math.min(api.size([0, 1])[1] * 0.6, 50);

      const rectShape = this.$echarts.graphic.clipRectByRect(
        {
          x: start[0],
          y: start[1] - height / 2,
          width: end[0] - start[0],
          height: height
        },
        {
          x: params.coordSys.x,
          y: params.coordSys.y,
          width: params.coordSys.width,
          height: params.coordSys.height
        }
      );
      return (
        rectShape && {
          type: 'rect',
          transition: ['shape'],
          shape: rectShape,
          style: api.style()
        }
      );
    }
  }
};
