<template>
<el-container>
  <el-header>
    <HeaderTitle />
  </el-header>
  <el-main>
     <header-search
        :show-primary-search="true"
        @refreshTableData="refreshTableData"
        cssType="1"
      >
    </header-search>
    <div class='search-fixed'>
        <div style="width:100%;" >
          <span>{{EQUIPMENT_STATUS_GANTT.DATE_RANGE}}</span>
          <div class='width-special'>
            <el-date-picker
              class="dateTime"
              v-model="dateTime"
              type="daterange"
              align="left"
              size="mini"

              value-format="yyyy-MM-dd HH:mm:ss"
              :range-separator="BTN_TEXT.TO"
              :start-placeholder="BTN_TEXT.STARTTIME"
              :end-placeholder="BTN_TEXT.ENDTIME"
              :picker-options="pickerOptions"
              :default-time="['00:00:00', '23:59:59']"
            ></el-date-picker>
          <!-- <span>班次范围</span>
          <el-select v-model="shiftData"  size="mini" placeholder="请选择">
            <el-option
              v-for="item in shiftOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select> -->
          <el-button type="default" style="margin-left:5px;"  size="mini" @click="searchShift()" >查询</el-button>
          </div>
        </div>
      </div>
    <gantt-chart
      :chart-data='chartData'
      :loading="loading"/>
    <vxe-pager
      align="right"
      :current-page.sync="current"
      :page-size.sync='pageSize'
      :total="total"
      :layouts="[
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ]"
      :page-sizes="[5,10,25,50]"
      border
      @page-change="pageChange"
      perfect
      >
    </vxe-pager>
  </el-main>
</el-container>
</template>

<script>
import js from '@/assets/js/report/EquipmentGantt_andon/index';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/report/EquipmentGantt/index.less';
</style>
