import { showErrorNotify } from '@/utils/notify';
import { showWarningMessage } from '@/utils/message';
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import GanttChart from '@/views/report/EquipmentGantt_andon/GanttChart.vue';
import {
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  EQUIPMENT_STATUS_GANTT
} from '@/utils/constantsFile';
import { CommonService } from '@/services/common.service';

import {
  GetAndonEquipmentGantt,
  GetEquipmentGanttQueryParameter

} from '@/api/equipmentstatus';
import { searchCondetionFunc } from '@/utils/publicFunction';
// 所搜框
import SearchBox from '@/components/common/SearchBox';
import { configurationGetConfigurationByEnum } from '@/api/statusEquipmentReport.js';
export default {
  name: 'EquipmentGantt',
  components: {
    HeaderTitle,
    GanttChart,
    SearchBox
  },
  data() {
    return {
      EQUIPMENT_STATUS_GANTT: EQUIPMENT_STATUS_GANTT,
      BTN_TEXT: BTN_TEXT,
      // 搜索框参数
      searchList: [],
      dateTime: [],
      loading: false,
      total: 0,
      pageSize: 5,
      current: 1,
      // 甘特图数据
      chartData: null,
      isSearchBoxShow: false,
      pickerOptions: {

        disabledDate: (current) => {
          return current.getTime() > this.moment(`${this.moment(Date.now()).format('YYYY-MM-DD')} 23:59:59`).format('x');
        }

      },
      // 搜索框选择的搜索条件
      searchData: {
        conditionJson1: []
      },
      // 保存搜索内输入的搜索条件，防止翻页时变回无条件查询
      savedSearchCondition: [],
      shiftOptions: [],
      shiftData: null
    };
  },
  created() {
    // this.getSystemShift();
    this.getQueryParameter();
  },
  mounted() {
    this.findList();
  },

  methods: {
    complexSearch(conditionArr1, groupOperator, conditionArr2) {
      const {
        conditionArr1: conditionJson1,
        groupOperator: operator,
        conditionArr2: conditionJson2
      } = CommonService.generateSearchCondition(
        conditionArr1,
        groupOperator,
        conditionArr2
      );
      this.searchData = {
        conditionJson1,
        operator,
        conditionJson2
      };
      this.pageChange({
        type: 'size',
        currentPage: 1,
        pageSize: 5
      });
    },
    showSearchCard() {
      this.isSearchBoxShow = !this.isSearchBoxShow;
    },
    pageChange(params) {
      if (params.type === 'size') {
        this.current = params.currentPage;
        this.pageSize = params.pageSize;
      }

      this.findList();
    },
    /**
     * @description: 获取搜索框参数
     * @param {*}
     * @return {*}
     */
    async getQueryParameter() {
      try {
        const res = await GetEquipmentGanttQueryParameter();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.searchList = res.rows;
        } else {
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      }
    },

    reset() {
      this.dateTime = [];
    },
    searchShift() {
      this.pageSize = 5;
      this.current = 1;
      this.findList();
    },
    async findList() {
      let endTime = `${this.moment(new Date()).format('YYYY-MM-DD HH:mm:ss')}`;
      let startTime = `${this.moment(new Date()).format('YYYY-MM-DD')} 00:00:00`;

      if (this.dateTime[0] !== undefined) {
        startTime = this.dateTime[0];
        endTime = this.dateTime[1];
      }

      var conditionJson = {
        conditionJson1: [],
        operatorGroup: 1,
        conditionJson2: []
      };
      if (this.savedSearchCondition.length > 0) {
        conditionJson = searchCondetionFunc(...this.savedSearchCondition);
      }

      var param = {
        size: this.pageSize,
        current: this.current,
        startTime,
        endTime
      };

      if (this.shiftData !== null) {
        // eslint-disable-next-line no-unused-expressions
        param.shiftStart = this.shiftOptions[this.shiftData].shiftStart;
        param.shiftEnd = this.shiftOptions[this.shiftData].shiftEnd;
      }

      try {
        this.loading = true;
        const res = await GetAndonEquipmentGantt(param, conditionJson);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.chartData = res.rows;
          this.total = res.total;
        } else {
          this.chartData = null;
          this.total = 0;
          showWarningMessage(res.message);
        }
      } catch (error) {
        this.chartData = null;
        this.total = 0;
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.loading = false;
      }
    },
    /**
     * @description: 刷新表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableData(savedSearchCondition) {
      if (savedSearchCondition) {
        this.savedSearchCondition = [...savedSearchCondition];
        this.current = 1;
      }
      this.findList();
    },
    async getSystemShift() {
      try {
        const data = await configurationGetConfigurationByEnum({
          configurationTypeEnum: 'SHIFT_TIME'
        }, {});

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (data.rows.processConfiguration) {
            var jsonArr = JSON.parse(data.rows.processConfiguration);
            this.shiftOptions = [];
            this.shiftData = null;
            var index = 0;
            for (var key in jsonArr) {
              var json = {
                value: index,
                label: key + ' ' + jsonArr[key],
                shiftStart: jsonArr[key].split('-')[0],
                shiftEnd: jsonArr[key].split('-')[1]
              };
              ++index;
              this.shiftOptions.push(json);
            }
          }
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    }
  }
};
